"use client";
import React from "react";
import { LoginProps } from "../../types/login";
import LoginForm from "./login-form";
import Lottie from "./dotlottie";

const Login: React.FC<LoginProps> = ({
  onLogin,
  onForgotPassword,
  onSignUp,
  isLoading = false,
  error,
}) => {
  return (
    <div className="h-screen w-screen items-center bg-foreground overflow-hidden bg-linear-to-b from-foreground via-foreground to-main-bg">
      <div className="absolute top-6 left-6 z-20 flex items-center space-x-3">
        <div className="w-10 h-10 bg-white/20 backdrop-blur-sm border border-white rounded-lg flex items-center justify-center">
          <span className="text-white font-bold text-lg">N</span>
        </div>
        <span className="text-white font-semibold text-xl tracking-wide">
          NEXUS ORG
        </span>
      </div>
      <div className="flex flex-row items-center px-10 justify-around">
        <div className="bg-transparent h-fit w-fit items-center">
          <Lottie />
        </div>
        <div className="divider-horizontal py-6 divider min-h-screen"></div>
        <LoginForm
          onLogin={onLogin}
          onForgotPassword={onForgotPassword}
          onSignUp={onSignUp}
          isLoading={isLoading}
          error={error}
        />
      </div>
    </div>
  );
};

export default Login;
